html,
body {
  height: 100%;
}

html {
  font-size: 10px;
}

body {
  background: #fff;
  font-style: normal;
  font-weight: normal;
  font-size: var(--fs-default);
  line-height: 1.2;
  font-family: "Open Sans", Arial, sans-serif;
  color: #000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@media (max-width: 1199px) {
  body.is-menu-opened {
    overflow: hidden;
  }
}

a {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}
a:hover {
  text-decoration: underline;
}

input,
button,
select,
textarea {
  font-weight: normal;
  font-family: "Open Sans", Arial, sans-serif;
}

input,
button {
  appearance: none;
}

picture,
time {
  display: block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: bold;
}

.wb-index {
  padding: 100px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.wb-index a {
  width: 100%;
  padding: 10px 0;
  display: block;
  font-size: 18px;
  text-align: center;
}

#site {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
}

svg,
.icon {
  width: 1em;
  height: 1em;
  display: inline-block;
  color: inherit;
  fill: currentColor;
}

.container {
  max-width: 1284px;
  margin: 0 auto;
  padding: 0 var(--gap);
}

.row {
  margin: 0 calc(-1 * var(--gap));
  display: flex;
  flex-wrap: wrap;
}
.row__col {
  max-width: 100%;
  min-height: 1px;
  padding: 0 var(--gap);
  flex: 1;
}
.row__col--4 {
  max-width: 33.333333333%;
  flex-basis: 33.333333333%;
}
.row__col--6 {
  max-width: 50%;
  flex-basis: 50%;
}

.img-cover {
  display: block;
  position: relative;
  overflow: hidden;
}
.img-cover img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
}

.text-overflow {
  max-width: 100%;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.text-clamp {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.text-clamp--2 {
  -webkit-line-clamp: 2;
}

.color-gray {
  color: #808080;
}

.area-click, .intro__body-close, .footer-nav__link, .header-nav__link, .contacts-media__link, .btn-copy {
  --indent: -5px;
  position: relative;
}
.area-click:before, .intro__body-close:before, .footer-nav__link:before, .header-nav__link:before, .contacts-media__link:before, .btn-copy:before {
  content: "";
  position: absolute;
  top: var(--indent);
  right: var(--indent);
  bottom: var(--indent);
  left: var(--indent);
}

.btn-reset, .intro__body-close, .header__toggle, .header__menu-btn-close, .btn-copy {
  background: transparent;
  margin: 0;
  padding: 0;
  font-size: 0;
  outline: none;
  border: none;
  border-radius: 0;
}

.btn-copy {
  width: 22px;
  height: 22px;
  display: block;
  border-radius: 4px;
  transition: all 0.24s;
  transition-property: opacity;
  cursor: pointer;
}
.btn-copy:hover {
  text-decoration: none;
}
@media (min-width: 1200px) {
  .btn-copy:hover {
    opacity: 0.4;
  }
}
.btn-copy__icon {
  width: 100%;
  height: 100%;
  display: block;
}

.btn-group__inner {
  margin: -8px;
  display: flex;
}
.btn-group__item {
  margin: 8px;
}

.input-profit {
  padding-right: 20px;
  position: relative;
}
