.btn__icon-wrapper {
  width: 22px;
  height: 22px;
  flex: 0 0 22px;
  position: relative;
}
.btn__icon-wrapper--ltr {
  margin-right: 8px;
}
.btn__icon-wrapper--rtl {
  margin-left: 8px;
}
.btn__icon {
  width: 100%;
  height: 100%;
