  top: 0;
  left: 0;
  transition: all 0.12s;
  transition-property: visibility, opacity;
}
.btn.is-active .btn__icon--normal, .btn:not(.is-active) .btn__icon--active {
  visibility: hidden;
  opacity: 0;
}

.input-el {
  background: #fff;
  width: 100%;
  min-height: 40px;
  padding: 8px;
  display: flex;
  align-items: center;
  font-weight: normal;
  font-size: var(--fs-7);
  line-height: 1.35;
  color: #000;
  border: 1px solid currentColor;
  border-radius: 7px;
  outline: none;
  box-shadow: 0 0 0 transparent;
  transition: all 0.24s;
  transition-property: background, box-shadow;
  position: relative;
}
@media (min-width: 1200px) {
