  border-color: transparent;
  pointer-events: none;
  user-select: none;
}
.input-el__link {
  margin-right: 20px;
  text-decoration: underline;
  user-select: initial;
  pointer-events: initial;
  transition: all 0.4s;
  transition-property: box-shadow;
}
.input-el__link:hover {
  text-decoration: none;
}
.input-el__btn-copy {
  flex: 0 0 var(--btn-size);
  margin-left: auto;
  pointer-events: auto;
